'use client';

import {
  AnimatePresence,
  motion,
  useMotionValueEvent,
  useScroll,
} from 'framer-motion';
import { CalendarIcon, ChevronDown } from 'lucide-react';
import { Link } from 'next-view-transitions';
import React, { useCallback, useLayoutEffect, useRef, useState } from 'react';

import { LocaleSwitcher } from '../locale-switcher';
import { Button } from '@/components/elements/button';
import { Logo } from '@/components/logo';
import { cn } from '@/lib/utils';

// Page objektum (Strapi Page) minim√°l shape a men√º dropdownhoz
type PageLike = {
  id: number;
  slug: string;
  dynamic_zone?: Array<{ __component: string; heading?: string | null }>;
};

// Men√º item (bal oldali men√º), opcion√°lis aloldalakkal (pages)
type NavItem = {
  id: number;
  URL: string;
  text: string;
  target?: string | null;
  pages?: PageLike[];
};

// Komponens props
type Props = {
  leftNavbarItems: NavItem[];
  rightNavbarItems: { URL: string; text: string; target?: string | null }[];
  logo: any;
  locale: string;
};

// Lokaliz√°lt href a nav itemhez ("/" ‚Üí "/hu", egy√©b ‚Üí "/hu/slug")
const navHref = (locale: string, url: string) =>
  url === '/'
    ? `/${locale}`
    : `/${locale}${url.startsWith('/') ? url : `/${url}`}`;

// Lokaliz√°lt href egy Strapi Page slughoz
const pageHref = (locale: string, p: PageLike) => `/${locale}/${p.slug}`;

// Men√º dropdownban a Page "Hero" heading/subheading felhaszn√°l√°sa (ha van)
function heroForPage(p: PageLike) {
  const hero = p.dynamic_zone?.find(
    (x) => x.__component === 'dynamic-zone.hero'
  ) as { heading?: string | null; sub_heading?: string | null } | undefined;

  return {
    heading: hero?.heading ?? p.slug,
    sub: (hero as any)?.sub_heading ?? null,
  };
}

const isBookingText = (t?: string | null) => {
  const s = (t ?? '').toLowerCase();
  return (
    s.includes('id≈ëpont') ||
    s.includes('idopont') ||
    s.includes('foglal') ||
    s.includes('booking') ||
    s.includes('appointment')
  );
};

/**
 * Safari-biztos 2 vonal -> X:
 * - NINCS ::before/::after
 * - NINCS Tailwind rotate/translate class a vonalakon
 * - Inline style √≠rja a top/transform-ot, √≠gy Safari nem "nyeli el"
 */
function MenuToggle({
  open,
  onClick,
  className,
}: {
  open: boolean;
  onClick: () => void;
  className?: string;
}) {
  const LINE_W = 40;
  const LINE_H = 2;
  const OFFSET = 3;
  const BTN = 40;

  const baseLineStyle: React.CSSProperties = {
    position: 'absolute',
    left: '50%',
    width: LINE_W,
    height: LINE_H,
    marginLeft: -(LINE_W / 2),
    backgroundColor: '#E7E5E4',
    display: 'block',
    transformOrigin: 'center',
    willChange: 'transform, top, opacity',
    transitionProperty: 'transform, top, opacity',
    transitionDuration: '320ms',
    transitionTimingFunction: 'cubic-bezier(.22,1,.36,1)',
  };

  const topClosed1 = BTN / 2 - OFFSET;
  const topClosed2 = BTN / 2 + OFFSET;
  const topOpen = BTN / 2;

  const line1: React.CSSProperties = {
    ...baseLineStyle,
    top: open ? topOpen : topClosed1,
    transform: open
      ? 'translateZ(0) rotate(45deg)'
      : 'translateZ(0) rotate(0deg)',
  };

  const line2: React.CSSProperties = {
    ...baseLineStyle,
    top: open ? topOpen : topClosed2,
    transform: open
      ? 'translateZ(0) rotate(-45deg)'
      : 'translateZ(0) rotate(0deg)',
  };

  return (
    <button
      type="button"
      aria-label={open ? 'Men√º bez√°r√°sa' : 'Men√º megnyit√°sa'}
      aria-expanded={open}
      onClick={onClick}
      className={cn(
        'relative h-10 w-10 shrink-0 touch-manipulation',
        'bg-transparent',
        'active:scale-[0.98] transition-transform',
        className
      )}
      style={{ WebkitTapHighlightColor: 'transparent' }}
    >
      <span aria-hidden style={line1} />
      <span aria-hidden style={line2} />
    </button>
  );
}

export const MobileNavbar = ({
  leftNavbarItems,
  rightNavbarItems,
  logo,
  locale,
}: Props) => {
  const [open, setOpen] = useState(false);
  const [openId, setOpenId] = useState<number | null>(null);

  const closeMenu = useCallback(() => {
    setOpen(false);
    setOpenId(null);
  }, []);

  const toggleMenu = useCallback(() => {
    setOpen((v) => {
      const next = !v;
      if (!next) setOpenId(null);
      return next;
    });
  }, []);

  const { scrollY } = useScroll();
  const [bg, setBg] = useState(false);
  useMotionValueEvent(scrollY, 'change', (v) => setBg(v > 100));

  const leftItems = leftNavbarItems ?? [];
  const rightItems = rightNavbarItems ?? [];

  // iOS/Safari stabil: translateZ(0) a framer transform mell√©
  const gpu = (_: any, t: string) => `translateZ(0) ${t}`;
  const EASE = [0.22, 1, 0.36, 1] as const;

  // ‚úÖ Layout-jump fix: m√©rt magass√°g (egy dropdown nyitva egyszerre)
  const contentRef = useRef<HTMLDivElement | null>(null);
  const [contentH, setContentH] = useState(0);

  useLayoutEffect(() => {
    if (!openId) return;
    const el = contentRef.current;
    if (!el) return;
    setContentH(el.scrollHeight || 0);

    const onResize = () => setContentH(el.scrollHeight || 0);
    window.addEventListener('resize', onResize, { passive: true });
    return () => window.removeEventListener('resize', onResize);
  }, [openId]);

  return (
    <>
      {/* Overlay */}
      <AnimatePresence initial={false}>
        {open && (
          <motion.div
            key="mobile-menu"
            transformTemplate={gpu}
            initial={{ opacity: 0, y: 16, scale: 0.985 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 12, scale: 0.99 }}
            transition={{ duration: 0.3, ease: EASE }}
            className={cn(
              'fixed inset-0 z-50',
              // ‚úÖ fekete √ºveg: blur akkor l√°tszik, ha √°ttetsz≈ëbb fekete van t√°mogatott b√∂ng√©sz≈ën
              'bg-black/90 supports-[backdrop-filter]:bg-black/40',
              'backdrop-blur-2xl backdrop-saturate-150',
              'ring-1 ring-white/10'
            )}
            style={{
              WebkitBackdropFilter: 'blur(28px) saturate(150%)',
              backdropFilter: 'blur(28px) saturate(150%)',
              willChange: 'opacity, transform',
            }}
          >
            <div className="h-full pt-20">
              <div className="flex w-full justify-end px-8 pt-4">
                <LocaleSwitcher currentLocale={locale} />
              </div>

              <motion.div
                layout
                className="mt-6 flex w-full flex-col items-start justify-start gap-[14px] px-8"
              >
                {leftItems.map((item) => {
                  const pages = item.pages ?? [];
                  const isOpen = openId === item.id;

                  if (pages.length === 0) {
                    return (
                      <motion.div layout key={item.id} className="relative">
                        <Link
                          href={navHref(locale, item.URL)}
                          onClick={closeMenu}
                        >
                          <span className="block text-4xl text-primary">
                            {item.text}
                          </span>
                        </Link>
                      </motion.div>
                    );
                  }

                  return (
                    <motion.div layout key={item.id} className="w-full">
                      <button
                        type="button"
                        className="flex items-center gap-2"
                        onClick={() => setOpenId(isOpen ? null : item.id)}
                        style={{ WebkitTapHighlightColor: 'transparent' }}
                      >
                        <span className="block text-4xl text-primary">
                          {item.text}
                        </span>

                        <motion.span
                          aria-hidden
                          initial={false}
                          animate={isOpen ? 'open' : 'closed'}
                          transformTemplate={gpu}
                          style={{
                            display: 'inline-flex',
                            willChange: 'transform',
                          }}
                          variants={{
                            open: {
                              rotate: 180,
                              transition: { duration: 0.24, ease: EASE },
                            },
                            closed: {
                              rotate: 0,
                              transition: { duration: 0.32, ease: EASE },
                            },
                          }}
                        >
                          <ChevronDown className="h-5 w-5 text-primary" />
                        </motion.span>
                      </button>

                      <motion.div
                        layout
                        initial={false}
                        animate={{
                          height: isOpen ? contentH : 0,
                          opacity: isOpen ? 1 : 0,
                          y: isOpen ? 0 : 6,
                          marginTop: isOpen ? 12 : 0,
                        }}
                        transition={{
                          duration: isOpen ? 0.22 : 0.34,
                          ease: EASE,
                        }}
                        transformTemplate={gpu}
                        style={{
                          overflow: 'hidden',
                          pointerEvents: isOpen ? 'auto' : 'none',
                          willChange: 'height, opacity, transform',
                          WebkitBackfaceVisibility: 'hidden',
                          backfaceVisibility: 'hidden',
                        }}
                        className="ml-3"
                      >
                        <div
                          ref={(el) => {
                            if (isOpen) contentRef.current = el;
                          }}
                          className="flex flex-col gap-3 border-l border-white/15 pl-4"
                        >
                          <Link
                            href={navHref(locale, item.URL)}
                            onClick={closeMenu}
                            className="text-[18px] text-white/80"
                          >
                            √ñsszes
                          </Link>

                          {pages.map((p) => {
                            const hero = heroForPage(p);
                            return (
                              <Link
                                key={p.id}
                                href={pageHref(locale, p)}
                                className="block rounded-lg px-3 py-2 transition hover:bg-white/5"
                                onClick={closeMenu}
                              >
                                <div className="text-sm text-white">
                                  {hero.heading}
                                </div>
                                {hero.sub && (
                                  <div className="mt-0.5 line-clamp-2 text-xs text-white/60">
                                    {hero.sub}
                                  </div>
                                )}
                              </Link>
                            );
                          })}
                        </div>
                      </motion.div>
                    </motion.div>
                  );
                })}
              </motion.div>

              {/* CTA row (id≈ëpontfoglal√°s gomb: calendar + ny√≠l) */}
              <div className="mt-8 flex w-full flex-row items-start gap-2.5 px-8 py-4">
                {rightItems.map((item, i) => {
                  const booking = isBookingText(item.text);
                  const isPrimary = i === rightItems.length - 1;

                  return (
                    <Button
                      key={item.text}
                      variant={isPrimary ? 'primary' : 'simple'}
                      as={Link}
                      href={navHref(locale, item.URL)}
                      onClick={closeMenu}
                      className={cn(booking && 'group w-full py-4')}
                    >
                      {booking ? (
                        <span className="inline-flex items-center justify-center gap-2">
                          <CalendarIcon className="h-4 w-4" />
                          <span>{item.text}</span>
                          {/* hero-szer≈± ny√≠l reveal */}
                          <span className="w-0 overflow-hidden transition-all duration-300 ease-in-out group-hover:w-5">
                            ‚Üí
                          </span>
                        </span>
                      ) : (
                        item.text
                      )}
                    </Button>
                  );
                })}
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Header */}
      <div
        className={cn(
          'relative z-50 flex w-full items-center justify-between rounded-md px-2.5 py-1.5 transition duration-200',
          bg
            ? 'bg-neutral-900 shadow-[0px_-2px_0px_0px_var(--neutral-800),0px_2px_0px_0px_var(--neutral-800)]'
            : 'bg-transparent'
        )}
        style={{ ['--mobile-nav-h' as any]: '56px' }}
      >
        <Logo locale={locale} image={logo?.image} company={logo?.company} />
        <MenuToggle open={open} onClick={toggleMenu} />
      </div>
    </>
  );
};
