'use client';

import {
  AnimatePresence,
  motion,
  useMotionValueEvent,
  useScroll,
} from 'framer-motion';
import { ChevronDown } from 'lucide-react';
import { Link } from 'next-view-transitions';
import { usePathname } from 'next/navigation';
import { useMemo, useState } from 'react';

import { LocaleSwitcher } from '../locale-switcher';
import { NavbarItem } from './navbar-item';
import { Button } from '@/components/elements/button';
import { Logo } from '@/components/logo';
import { cn } from '@/lib/utils';

type PageLike = {
  id: number;
  slug: string;
  dynamic_zone?: Array<{
    __component: string;
    heading?: string | null;
    sub_heading?: string | null;
  }>;
};

type NavItem = {
  id: number;
  URL: string;
  text: string;
  target?: string | null;
  pages?: PageLike[];
};

type Props = {
  leftNavbarItems: NavItem[];
  rightNavbarItems: { URL: string; text: string; target?: string | null }[];
  logo: any;
  locale: string;
};

const hrefForNav = (locale: string, url: string) =>
  url === '/' ? `/${locale}` : `/${locale}${url}`;
const hrefForPage = (locale: string, p: PageLike) => `/${locale}/${p.slug}`;

function heroForPage(p: PageLike) {
  const hero = p.dynamic_zone?.find(
    (x) => x.__component === 'dynamic-zone.hero'
  ) as { heading?: string | null; sub_heading?: string | null } | undefined;

  return {
    heading: hero?.heading ?? p.slug,
    sub: hero?.sub_heading ?? null,
  };
}

export const DesktopNavbar = ({
  leftNavbarItems,
  rightNavbarItems,
  logo,
  locale,
}: Props) => {
  const pathname = usePathname();
  const { scrollY } = useScroll();

  const [showBackground, setShowBackground] = useState(false);
  const [openId, setOpenId] = useState<number | null>(null);

  useMotionValueEvent(scrollY, 'change', (v) => setShowBackground(v > 100));

  // scroll ut√°n glass
  const wrapperGlass = showBackground
    ? cn(
        'bg-black/75 supports-[backdrop-filter]:bg-black/18',
        'supports-[backdrop-filter]:backdrop-blur-2xl supports-[backdrop-filter]:backdrop-saturate-150',
        'ring-1 ring-white/10 shadow-[0_12px_40px_rgba(0,0,0,0.35)]'
      )
    : 'bg-transparent';

  return (
    <motion.div
      initial={{ opacity: 0, y: -8, scale: 0.985 }}
      animate={{
        opacity: 1,
        y: 0,
        scale: 1,
        width: showBackground ? '84%' : '100%',
      }}
      transition={{
        opacity: { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
        y: { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
        scale: { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
        width: { duration: 0.42, ease: [0.22, 1, 0.36, 1] },
      }}
      className={cn(
        'relative mx-auto flex w-full items-center justify-between px-4 py-3 rounded-xl',
        'transform-gpu will-change-[transform,opacity,width]',
        'transition-colors duration-200',
        wrapperGlass
      )}
    >
      {/* Bal: Logo (csak m√©ret fix, kin√©zet/m≈±k√∂d√©s v√°ltozatlan) */}
      <div className="flex items-center gap-2 z-30 shrink-0 [&_img]:h-8 [&_img]:w-auto [&_img]:object-contain [&_img]:block [&_img]:max-h-8 [&_svg]:h-8 [&_svg]:w-auto">
        <Logo locale={locale} image={logo?.image} company={logo?.company} />
      </div>

      {/* K√∂z√©p: menu (mindig k√∂z√©pen) */}
      <div className="pointer-events-none absolute inset-y-0 left-1/2 z-20 flex -translate-x-1/2 items-center">
        <div className="pointer-events-auto flex items-center gap-1.5 max-w-[min(760px,60vw)]">
          {leftNavbarItems.map((item) => {
            const pages = item.pages ?? [];
            const hasDropdown = pages.length > 0;

            if (!hasDropdown) {
              return (
                <NavbarItem
                  key={item.id}
                  href={hrefForNav(locale, item.URL) as never}
                  target={item.target ?? undefined}
                >
                  {item.text}
                </NavbarItem>
              );
            }

            const isOpen = openId === item.id;
            const isChildActive = pages.some((p) =>
              pathname?.includes(hrefForPage(locale, p))
            );

            // ‚Äúkiemeltek‚Äù = els≈ë 3 page
            const featured = pages.slice(0, 3);

            return (
              <div
                key={item.id}
                className="relative"
                onMouseEnter={() => setOpenId(item.id)}
                onMouseLeave={() => setOpenId(null)}
              >
                <button
                  type="button"
                  className={cn(
                    'flex items-center justify-center text-sm leading-[110%] px-4 py-2 rounded-full',
                    'text-white transition duration-200',
                    'hover:bg-white/10',
                    isChildActive && 'bg-white/10'
                  )}
                >
                  <span>{item.text}</span>
                  <ChevronDown
                    className={cn(
                      'ml-1 h-4 w-4 text-white/80 transition duration-300',
                      isOpen && 'rotate-180'
                    )}
                  />
                </button>

                <AnimatePresence>
                  {isOpen && (
                    <motion.div
                      role="menu"
                      initial={{ opacity: 0, y: 10, scale: 0.985 }}
                      animate={{ opacity: 1, y: 0, scale: 1 }}
                      exit={{ opacity: 0, y: 8, scale: 0.99 }}
                      transition={{ duration: 0.16, ease: [0.22, 1, 0.36, 1] }}
                      className={cn(
                        'absolute left-0 top-full mt-3 z-50 w-[720px] max-w-[78vw] overflow-hidden',
                        'rounded-2xl',
                        // ‚úÖ CSAK ez v√°ltozott: s√∂t√©tebb h√°tt√©r, hogy ne √ºss√∂n √°t
                        'bg-neutral-950/95 supports-[backdrop-filter]:bg-neutral-950/62',
                        'supports-[backdrop-filter]:backdrop-blur-2xl supports-[backdrop-filter]:backdrop-saturate-150',
                        'ring-1 ring-white/10 shadow-[0_26px_70px_rgba(0,0,0,0.45)]'
                      )}
                    >
                      <div className="grid grid-cols-12 gap-0">
                        {/* Left: lista */}
                        <div className="col-span-7 p-2">
                          <Link
                            href={hrefForNav(locale, item.URL)}
                            className="block rounded-xl px-3 py-2 text-sm text-white/85 hover:text-white hover:bg-white/8 transition"
                            onClick={() => setOpenId(null)}
                          >
                            √ñsszes
                          </Link>

                          <div className="my-2 h-px bg-white/10" />

                          <div className="grid grid-cols-1 gap-1">
                            {pages.map((p) => {
                              const hero = heroForPage(p);
                              const active = pathname?.includes(
                                hrefForPage(locale, p)
                              );

                              return (
                                <Link
                                  key={p.id}
                                  href={hrefForPage(locale, p)}
                                  className={cn(
                                    'block rounded-xl px-3 py-2 transition',
                                    active ? 'bg-white/10' : 'hover:bg-white/8'
                                  )}
                                  onClick={() => setOpenId(null)}
                                >
                                  <div className="text-sm text-white">
                                    {hero.heading}
                                  </div>
                                  {hero.sub && (
                                    <div className="mt-0.5 text-xs text-white/60 line-clamp-2">
                                      {hero.sub}
                                    </div>
                                  )}
                                </Link>
                              );
                            })}
                          </div>
                        </div>

                        {/* Right: showcase / featured */}
                        <div className="col-span-5 border-l border-white/10 p-3">
                          <div className="mb-2 flex items-center justify-between">
                            <div className="text-xs uppercase tracking-wider text-white/60">
                              Kiemelt
                            </div>
                            <div className="text-xs text-white/45">
                              {item.text}
                            </div>
                          </div>

                          <div className="space-y-2">
                            {featured.map((p) => {
                              const hero = heroForPage(p);

                              return (
                                <Link
                                  key={p.id}
                                  href={hrefForPage(locale, p)}
                                  onClick={() => setOpenId(null)}
                                  className={cn(
                                    'group block rounded-2xl p-3 transition',
                                    'bg-white/5 hover:bg-white/10',
                                    'ring-1 ring-white/10'
                                  )}
                                >
                                  {/* ‚Äúk√©p‚Äù helyett pr√©mium gradient placeholder (adat n√©lk√ºl) */}
                                  <div className="relative overflow-hidden rounded-xl">
                                    <div className="h-20 w-full bg-gradient-to-br from-white/10 via-white/5 to-transparent" />
                                    <div className="absolute inset-0 bg-[radial-gradient(circle_at_30%_20%,rgba(255,255,255,0.18),transparent_55%)]" />
                                  </div>

                                  <div className="mt-2">
                                    <div className="text-sm text-white group-hover:text-white">
                                      {hero.heading}
                                    </div>
                                    {hero.sub && (
                                      <div className="mt-0.5 text-xs text-white/60 line-clamp-2">
                                        {hero.sub}
                                      </div>
                                    )}
                                  </div>
                                </Link>
                              );
                            })}
                          </div>

                          <div className="mt-3 text-xs text-white/50">
                            Tipp: ide k√©s≈ëbb tehetsz val√≥di cover k√©pet
                            Strapi-b√≥l (featured image), √©s ez 11/10 lesz.
                          </div>
                        </div>
                      </div>
                    </motion.div>
                  )}
                </AnimatePresence>
              </div>
            );
          })}
        </div>
      </div>

      {/* Jobb: Locale + CTA */}
      <div className="flex items-center space-x-2 z-30">
        <LocaleSwitcher currentLocale={locale} />

        {rightNavbarItems.map((item, index) => (
          <Button
            key={item.text}
            variant={
              index === rightNavbarItems.length - 1 ? 'primary' : 'simple'
            }
            as={Link}
            href={hrefForNav(locale, item.URL)}
          >
            {item.text}
          </Button>
        ))}
      </div>
    </motion.div>
  );
};
